.center {
  text-align: center;
}
.empty {
  height: 40px;
}

.grid {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  width: calc(120px * 8 + 20px * 8);
  //padding: 10px;
}

.item {
  user-select: none;
  box-sizing: border-box;
  background-color: #262626;
  width: 120px;
  border-radius: 8px;
  overflow: hidden;
  margin: 10px;

  .name {
    text-align: center;
    background-color: #333;
    line-height: 32px;
  }
  .content {
    display: flex;

    .left,
    .right {
      width: 60px;
      font-size: 40px;
      line-height: 64px;
      text-align: center;

      &.multiple {
        display: flex;
        font-size: 32px;

        .letter {
          width: 30px;
        }
      }
    }

    .whole {
      width: 120px;
      font-size: 40px;
      line-height: 64px;
      text-align: center;
    }
  }
}

.letter {
  transition-duration: 50ms;

  &:hover {
    background-color: #2c2c2c;
    cursor: pointer;
  }
  &:active {
    background-color: #888;
    color: #222;
  }
}
